export default "/**\n * 那一年我二十三岁，活在自己的黄金时代，爱上了发着光的她。\n */\n\n#style-text {\n  position: absolute;\n  font-size: smaller;\n  -webkit-transform-origin: left;\n  height: 80%;\n  overflow: hidden;\n}\n\n/**\n * 多年以后，面对枕边的她，\n * 我都会回想起 2014 年国庆节后那个周末里我们的邂逅，\n * 那该是神的祝福。\n */\n\npre, a {\n  color: blue;\n  /* 如这不断变得优雅的代码，我们经历了从朴素到华丽再到\"一切都是刚刚好\"。*/\n}\n\npre:not(:empty) {\n  /*max-height: 66.6%;*/\n  width: 30%;\n  overflow: auto;\n  font-family: monospace;\n  padding: 10px 10px 20px;\n  box-shadow: -4px 4px 2px 0 rgba(0, 0, 0, 0);\n  white-space: pre-wrap;\n  outline: 0;\n}\n\n/**\n * 如果嫁给一个程序员有什么快乐的话，被动接受代码的黑魔法，算是吧。\n * 在过去的这些年里，我教她写过很多代码。\n * 不过呢，可能她一个也没学会。\n * \"你会就可以了，我需要的时候你来帮我做。\"\n * 这就是\"爱情\"的模样。\n */\n\n/**\n * 有人住高楼，有人在深沟，\n * 有人光万丈，有人一身锈，\n * 世人万千种，浮云莫去求，\n * 斯人若彩虹，遇上方知有。\n */\n\n.comment { color: #86B;}\n\n.selector { color: #E69F0F; }\n\n.selector .property { color: #64D5EA; }\n\n.property { color: #64D5EA; }\n\n.punctuation { color: #BE84F2; }\n\npre:not(#style-text) {\n  -webkit-transform: rotateY(10deg);\n  -webkit-transform-origin: left;\n}\n\n/**\n * 爱一个人之前，爱惜自己每一片技能的羽毛。\n * 爱上一个人后，但求平安顺遂，一生相伴。\n * 愿你身边那人，永远是我。\n */\n\n/*# sourceMappingURL=data:application/json;base64, */";